<template>
    <div class="header-alert" v-if="show">
        <div class="main">
            <div class="top">
                <span>创建你的歌单❤</span>
                <img id="cancel" src="../assets/img/cancel.png" @click="closeEvent">
            </div>
            <div class="content">
                <form>
                    <!-- <input type="text" value="" v-model="type" placeholder="1"> -->
                    <input type="text" v-model="musicListName" placeholder="请输入你的歌单名称">
                    <!-- <img id="loading" v-show="isLoading" src="../assets/img/loading.gif" alt=""> -->
                </form>
                    <!-- <img id="loading" v-show="isLoading" src="../assets/img/loading.gif" alt="">                 -->
                <!-- <el-progress :percentage="100" :format="format"></el-progress> -->
            </div>
            <div class="btn">
                <button class="submit" @click="submitForm($event)">创 建</button>
            </div>
        </div>
    </div>
</template>

<script>
import api from '../assets/config/api';
import axios from "axios";
//import "../../node_modules/bootstrap/dist/css/bootstrap.min.css"
export default {
    props:["show"],
    data() {
        return {
            // isLoading: false,
            musicListName: ""
        }
    },
    methods: {
        closeEvent(){
            this.$emit("update:show",false)
        },
       
        async submitForm(event) {
            event.preventDefault();
            // if(this.musicFile!=""){
            //     this.isLoading = true;
            // }
            
            let formData = new FormData();
            formData.append('musicListName', this.musicListName);
            console.log(formData.get('musicListName'));
 
            // let config = {
              // headers: { 
              //   'Content-Type': 'multipart/form-data'
              // }
            // }
            var that = this;
            await axios.post(api.AddList, {
                "musicListName": this.musicListName
            })
            .then(function (res) {
                
             console.log(res);
                if(res.data.code === 200){
                    // 弹窗TODO
                    alert("创建成功")
                    that.$emit("addClick")
                    that.$emit("update:show",false)
                }
                else if(res.data.code === 400){
                    alert("已存在该歌单！")
                }
              
            })
          }
    }

}
</script>

